@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/alumnisans-semibold.woff2") format("woff2"), url("../fonts/alumnisans-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/alumnisans-bold.woff2") format("woff2"), url("../fonts/alumnisans-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-light.woff2") format("woff2"), url("../fonts/robotocondensed-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 10px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  vertical-align: middle;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-block {
  position: relative;
  color: #ffffff;
  background-image: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1199px) {
  .main-block {
    background-image: conic-gradient(from 139.4deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}
@media (max-width: 767px) {
  .main-block {
    background-image: conic-gradient(from 171.43deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}

.main-block__container {
  display: flex;
  width: 1180px;
  max-width: 100%;
  padding-top: 51px;
  padding-bottom: 51px;
}
@media (max-width: 1199px) {
  .main-block__container {
    width: 100%;
    padding-top: 82px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .main-block__container {
    padding-top: 115px;
    padding-bottom: 80px;
  }
}

.main-block__wrapper {
  width: 39.6%;
  display: grid;
  grid-template-columns: 28.6% 58%;
  grid-template-rows: repeat(2, min-content);
}
@media (max-width: 1199px) {
  .main-block__wrapper {
    width: 52.2%;
    grid-template-columns: 32% 60%;
  }
}
@media (max-width: 767px) {
  .main-block__wrapper {
    max-width: 100%;
    width: 100%;
    grid-template-columns: 32% 68%;
  }
}

.main-block__text {
  margin-top: 305px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
}
@media (max-width: 1199px) {
  .main-block__text {
    margin-top: 186px;
  }
}
@media (max-width: 767px) {
  .main-block__text {
    margin-top: 118px;
    margin-right: -20px;
  }
}

.main-block__title-wrapper {
  margin-bottom: 49px;
}
@media (max-width: 1199px) {
  .main-block__title-wrapper {
    margin-bottom: 36px;
  }
}
@media (max-width: 767px) {
  .main-block__title-wrapper {
    margin-bottom: 23px;
  }
}

.main-block__title {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .main-block__title {
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
  }
}

.main-block__text-city {
  display: block;
  margin-top: 113px;
  margin-bottom: 12px;
  margin-right: 49px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  text-align: right;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .main-block__text-city {
    margin-top: 51px;
    margin-bottom: 5px;
    margin-right: 3px;
    font-size: 34px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .main-block__text-city {
    margin-top: -5px;
    margin-bottom: 12px;
    margin-right: 1px;
    font-size: 28px;
    line-height: 34px;
  }
}

.main-block__link {
  display: flex;
  justify-content: center;
  justify-self: end;
  width: 88%;
  grid-column: 2;
  padding: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  border: 4px solid #ffffff;
}
@media (max-width: 767px) {
  .main-block__link {
    width: 100%;
    grid-column: 1/3;
  }
}

@media (max-width: 1199px) {
  .main-block__img-wrapper {
    position: relative;
    width: 324px;
    height: 390px;
    overflow: hidden;
  }
}
@media (max-width: 767px) {
  .main-block__img-wrapper {
    display: none;
  }
}

@media (max-width: 1199px) {
  .main-block__img {
    position: absolute;
    left: -15px;
    max-width: none;
    width: 356px;
    height: 398px;
  }
}

.main-header {
  position: absolute;
  left: 0;
  right: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  background-color: #102654;
}

.main-header__container {
  display: flex;
  justify-content: space-between;
  width: 1180px;
  max-width: 100%;
  padding-top: 30px;
}
@media (max-width: 1199px) {
  .main-header__container {
    width: 100%;
    padding-top: 34px;
  }
}
@media (max-width: 767px) {
  .main-header__container {
    padding-top: 16px;
  }
}

.main-header__logo {
  margin-top: 10px;
  z-index: 1;
}
@media (max-width: 1199px) {
  .main-header__logo {
    width: 82px;
    height: 17px;
    margin-top: 7px;
  }
}
@media (max-width: 767px) {
  .main-header__logo {
    width: 69px;
    height: 20px;
    margin-top: 5px;
  }
}

.main-header__tel {
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  color: #ffffff;
  transition: opacity 0.3s ease;
  z-index: 1;
}
@media (hover: hover) {
  .main-header__tel:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-header__tel.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1199px) {
  .main-header__tel {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 767px) {
  .main-header__tel {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

